import BgContainer from 'component/bg-container'
import React, { useEffect } from 'react'
import { Images } from 'utils/cdn-resource'
import GameCompletionDialog from './completion-dialog'
import GameHeader from './page-header'
import GameMain from './page-main'
import { useFlipCardGame } from './hooks/flip-card'
import { useBoolState } from './hooks/use-bool'

export const SUNNY_HOME = 'sunnyHome'
export const CHILD = 'child'
export const TOGETHER = 'together'
export const BROOM = 'broom'

const gameSetting = [
	{ name: SUNNY_HOME, content: Images.sunnyHome },
	{ name: CHILD, content: Images.child },
	{ name: TOGETHER, content: Images.together },
	{ name: BROOM, content: Images.broom }
]

export type GamePageProps = {}
const GamePage = (p: GamePageProps) => {
	const { cards, flipCardById, revealedCardNames, completed } = useFlipCardGame(gameSetting)
	const [completionDialogState, openCompletionDialog] = useBoolState(false)

	useEffect(() => {
		if (completed) {
			openCompletionDialog()
		}
	}, [completed, openCompletionDialog])

	return (
		<div>
			<BgContainer
				header={<GameHeader revealedCardNames={revealedCardNames} />}
				main={<GameMain cards={cards} onCardClick={c => flipCardById(c.id)} />}
			/>
			<GameCompletionDialog open={completionDialogState} />
		</div>
	)
}
export default GamePage
