import { makeStyles } from '@material-ui/core'
import { CSSProperties } from '@material-ui/core/styles/withStyles'

const gradient = (colors: string[]) => `linear-gradient(${colors.join(',')})`

const header: CSSProperties = { height: '16vh', paddingTop: '6vh' }
const container: CSSProperties = {
	height: '100vh',
	background: gradient(['rgb(82, 49, 101)', 'rgb(221, 164, 146)'])
}

export const useStyles = makeStyles({ container, header })
