import { ThemeProvider } from '@material-ui/core'
import GamePage from 'page/game-page'
import LandingPage from 'page/landing-page'
import StoryPage from 'page/story-page'
import React, { useEffect } from 'react'
import { BrowserRouter, Route } from 'react-router-dom'
import { theme } from 'style/theme'
import { executePreload } from 'utils/preload'

export const LANDING_PAGE = '/'
export const GAME_PAGE = '/game'
export const STORY_PAGE = '/story'

function App() {
	useEffect(() => {
		executePreload()
	}, [])
	return (
		<div className='App'>
			<ThemeProvider theme={theme}>
				<BrowserRouter>
					<Route path={LANDING_PAGE} exact>
						<LandingPage />
					</Route>
					<Route path={GAME_PAGE}>
						<GamePage />
					</Route>
					<Route path={STORY_PAGE}>
						<StoryPage />
					</Route>
				</BrowserRouter>
			</ThemeProvider>
		</div>
	)
}

export default App
