import { Box } from '@material-ui/core'
import HelpIcon from '@material-ui/icons/Help'
import Anim from 'component/anim'
import React from 'react'
import { useStyles } from './index.style'

export type QuestionAnswerProps = {
	revealed?: boolean
	answer: JSX.Element
}
const QuestionAnswer = (p: QuestionAnswerProps) => {
	const styles = useStyles()
	return p.revealed ? (
		<Anim type='bounceIn'>
			<Box display='flex' justifyContent='center' alignItems='center' className={styles.answer}>
				{p.answer}
			</Box>
		</Anim>
	) : (
		<HelpIcon className={styles.question} />
	)
}
export default QuestionAnswer
