import React from 'react'

export type AnimType = 'bounce' | 'bounceIn'

export type AnimProps = {
	type: AnimType
	children: JSX.Element
	duration?: number
	delay?: number
	repeat?: boolean
}

const Anim = (p: AnimProps) => {
	return (
		<div
			className={`
        animate__animated
        animate__${p.type}
        ${p.repeat ? 'animate__infinite	infinite' : ''}
      `}
			style={{
				animationDuration: `${p.duration || 1}s`,
				animationDelay: `${p.delay || 0}s`
			}}
		>
			{p.children}
		</div>
	)
}

export default Anim
