import { Box, Dialog, DialogProps, Typography } from '@material-ui/core'
import { STORY_PAGE } from 'App'
import Anim from 'component/anim'
import React from 'react'
import { useHistory } from 'react-router-dom'
import { Images } from 'utils/cdn-resource'

const Gift = () => {
	const history = useHistory()
	const onGiftClick = () => {
		history.push(STORY_PAGE)
	}

	return (
		<Anim type='bounce' repeat>
			<Box
				onClick={onGiftClick}
				bgcolor='rgb(252, 227, 227)'
				borderRadius='50%'
				height='80px'
				width='80px'
				display='flex'
				justifyContent='center'
			>
				<img src={Images.gift} alt='gift' />
			</Box>
		</Anim>
	)
}

export type GameCompletionDialogProps = {} & Pick<DialogProps, 'open'>
const GameCompletionDialog = (p: GameCompletionDialogProps) => {
	return (
		<Dialog open={p.open}>
			<Box width='60vw' height='36vh' p={2}>
				<Box height='18vh' display='flex' justifyContent='center' alignItems='center'>
					<Gift />
				</Box>
				<Typography align='center' variant='h5'>
					解锁故事成功
				</Typography>
				<Box mb={1} />
				<Typography align='center' variant='body1'>
					恭喜完成挑战，符号的意义解锁中…
				</Typography>
			</Box>
		</Dialog>
	)
}
export default GameCompletionDialog
