import React from 'react'
import { Images } from 'utils/cdn-resource'

export type IconProps = {
	imgPath: string
}
export const Icon = (p: IconProps) => {
	return (
		<img
			style={{
				height: '28pt',
				width: '25pt'
			}}
			src={p.imgPath}
			alt='icon'
		></img>
	)
}

export const IconBroom = () => <Icon imgPath={Images.iconBroom} />
export const IconChild = () => <Icon imgPath={Images.iconChild} />
export const IconSunnyHome = () => <Icon imgPath={Images.iconSunnyHome} />
export const IconTogether = () => <Icon imgPath={Images.iconTogether} />
