import { colors, makeStyles } from '@material-ui/core'
import { Images } from 'utils/cdn-resource'

const url = (img: string) => `url(${img})`
const bgImg = Images.bg

const cardHeight = '130px'
const cardWidth = '160px'

type CardStyleProps = {
	frontSideImg: string
}

const card = {
	height: cardHeight,
	width: cardWidth,
	border: '1px solid',
	borderColor: colors.amber[200],
	borderRadius: '12%',
	transition: 'all 0.2s ease-in',
	backgroundImage: (p: CardStyleProps) => url(p.frontSideImg),
	backgroundSize: 'cover',
	backgroundRepeat: 'no-repeat'
}

const cardRevealed = {
	...card,
	transform: 'rotateY(180deg)',
	backgroundImage: url(bgImg)
}

export const useStyles = makeStyles({
	card,
	cardRevealed
})
