import * as R from 'ramda'
import { useEffect, useMemo, useState } from 'react'
import { shuffleArray } from 'utils/randomizer'

export type FlipCardType = {
	id: number
	name: string
	content: string
	revealed: boolean
	locked: boolean
}

const delayTimeForPlayingFlippingAnim = 400

export const useFlipCardGame = (hookParams: Pick<FlipCardType, 'content' | 'name'>[]) => {
	const initCards = hookParams.concat(hookParams).map((p, index) => ({
		id: index,
		name: p.name,
		content: p.content,
		revealed: false,
		locked: false
	}))

	const [cards, setCards] = useState<FlipCardType[]>(shuffleArray(initCards))

	/** when there are 2 cards being revealed, the content of them will be checked */
	/** 1. same content -> keep them revealed & mark them locked (locked means not operable any more) */
	/** 2. different content -> hide them */
	useEffect(() => {
		const revealedCards = cards.filter(R.compose(R.not, R.prop('locked'))).filter(R.prop('revealed'))
		if (revealedCards.length !== 2) return
		const cardsContent = revealedCards.map(R.prop('content'))
		const eq = R.equals(cardsContent[0])
		const areSameCards = R.all(eq)(cardsContent)
		if (areSameCards)
			setTimeout(
				() => setCards(cards.map(c => (c.revealed ? { ...c, locked: true } : c))),
				delayTimeForPlayingFlippingAnim
			)
		else
			setTimeout(
				() => setCards(cards.map(c => (c.locked ? c : { ...c, revealed: false }))),
				delayTimeForPlayingFlippingAnim
			)
	}, [cards, setCards])

	const flipCardById = (id: FlipCardType['id']) => {
		const flipCardIfIdMatch = (c: FlipCardType) => (c.id === id ? { ...c, revealed: true } : c)
		const changedCards = cards.map(flipCardIfIdMatch)
		setCards(changedCards)
	}

	const revealedCardNames = useMemo(
		() => R.dropRepeats(cards.filter(R.prop('locked')).filter(R.prop('revealed')).map(R.prop('name')).sort()),
		[cards]
	)

	const completed = useMemo(() => cards.every(c => c.locked), [cards])

	return { cards, flipCardById, revealedCardNames, completed }
}
