import React from "react";
import { useStyles } from "./index.style";

export type FlipCardProps = {
  revealed: boolean;
  frontSideImg: string;
};

const FlipCard = (p: FlipCardProps) => {
  const styles = useStyles({ frontSideImg: p.frontSideImg });
  return (
    <div
      role="none"
      className={p.revealed ? styles.card : styles.cardRevealed}
    />
  );
};

export default FlipCard;
