import * as R from 'ramda'
import { Box, Container } from '@material-ui/core'
import FlipCard from 'component/flip-card'
import React from 'react'
import { FlipCardType } from './hooks/flip-card'

export type GameMainProps = {
	cards: FlipCardType[]
	onCardClick: (p: FlipCardType) => any
}

const GameMain = (p: GameMainProps) => {
	const renderRow = (cards: FlipCardType[]) => (
		<Box display='flex' flexDirection='row'>
			{cards.map(c => (
				<Box m={1} onClick={() => p.onCardClick(c)} key={c.id}>
					<FlipCard revealed={c.revealed} frontSideImg={c.content} />
				</Box>
			))}
		</Box>
	)
	return (
		<Container>
			<Box display='flex' flexDirection='column' alignItems='center'>
				{renderRow(R.slice(0, 2, p.cards))}
				{renderRow(R.slice(2, 4, p.cards))}
				{renderRow(R.slice(4, 6, p.cards))}
				{renderRow(R.slice(6, 8, p.cards))}
			</Box>
		</Container>
	)
}
export default GameMain
